// Initialize Foundation 
$(document).foundation();


// Active Link Tracking - apply 'active' class to 
$(function() {
  $('nav a[href^="/' + location.pathname.split("/")[1] + '"]').addClass('active');
});


// Scroll to Top Button
$(function(){
	$(document).on( 'scroll', function(){
		if ($(window).scrollTop() > 300) {
			$('.scroll-top-wrapper').addClass('show');
		} else {
			$('.scroll-top-wrapper').removeClass('show');
		}
	});
	$('.scroll-top-wrapper').on('click', scrollToTop);
});
 
function scrollToTop() {
	verticalOffset = typeof(verticalOffset) != 'undefined' ? verticalOffset : 0;
	element = $('body');
	offset = element.offset();
	offsetTop = offset.top;
	$('html, body').animate({scrollTop: offsetTop}, 300, 'linear');
}

// JavaScript Scroll Reveal Animations
var srIndex = 0,
srLastPos = 0,
srCurrentPos;

var srConfig = {
  beforeReveal: function (domEl) {
      srCurrentPos = domEl.offsetTop;
      if (srLastPos == srCurrentPos) {
          srIndex++;
      }
      else {
          srIndex = 0;
      }
      srLastPos = srCurrentPos;
      domEl.className += ' delay-' + srIndex;
  }
};

// ScrollReveal https://scrollrevealjs.org/
window.sr = ScrollReveal(srConfig);

sr.reveal('.reveal-scroll', {
  origin: 'top',
  duration: 900,
  scale: 0.1,
});

sr.reveal('.reveal-scroll-alt', {
  origin: 'bottom',
  duration: 450,
  scale: 0.1,
});

sr.reveal('.reveal-slide-left', {
  origin: 'left',
  duration: 700,
  distance: '-9999px',
  easing: 'ease-in-out',
});

sr.reveal('.reveal-slide-right', {
  origin: 'right',
  duration: 700,
  distance: '-9999px',
  easing: 'ease-in-out',
});

sr.reveal('.reveal-slide-bottom', {
  origin: 'bottom',
  duration: 700,
  distance: '-9999px',
  easing: 'ease-in-out',
});

sr.reveal('.reveal-slow-fadein', {
  scale: 1.0,
  duration: 1000,
  distance: '0px',
  easing: 'ease-in-out',
});


// changethewords.js https://github.com/talut/changethewords
(function($) {
  $.fn.changeWords = function(options) {
    var settings = $.extend({
      time: 1500,
      animate: "zoomIn",
      selector: "span",
      repeat: true
    }, options);
    var wordCount = $(settings.selector, this).length;
    var words = $(settings.selector, this);
    words.filter(function() {
      return $(this).attr("data-id") != "1"
    }).css("display", "none");
    var count = 1;
    var changeThisWord = setInterval(function() {
      ++count;
      var wordOrder = count;
      words.filter(function() {
        return $(this).attr("data-id") == wordOrder
      }).addClass("animated " + settings.animate).css("display", "inline-block");
      words.filter(function() {
        return $(this).attr("data-id") != wordOrder
      }).css("display", "none").removeClass();
      if (count == wordCount) {
        count = 0;
      }
      if(count == 0 && settings.repeat != true)
      {
        clearInterval(changeThisWord);
      }
    }, settings.time);
  }
}(jQuery));

$(function() {
  $("#cycleTitle-home").changeWords({
    time: 3000,
    animate: "flash",
    selector: "span",
    repeat: false // true
  });
});